@import url(https://cdnjs.cloudflare.com/ajax/libs/antd/3.13.6/antd.min.css);
.login-form {
    width: 100%
}
.post-feed {
    margin-left: 20px
}

.job-input-field{
    margin: 10px;
}
.userInfoContainer{
    width: 20%;
    float: left;
